@font-face {
  font-family: "STARWARS";
  src: url("fonts/STARWARS.eot?") format("eot"),
    url("fonts/STARWARS.woff") format("woff"),
    url("fonts/STARWARS.ttf") format("truetype"),
    url("fonts/STARWARS.svg#STARWARS") format("svg");
  font-weight: normal;
  font-style: normal;
}

/*http://www.iconarchive.com/show/starwars-icons-by-sensibleworld/C3PO-icon.html*/
*, *:after, *:before {
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  color: white;
  height: 100%;
  background: rgba(60, 60, 60, 1);
}

h2 {
  color: white;
  /* text-shadow: 1px 2px 3px #000; */
  text-align: center;
}

.App {
  background: rgba(60, 60, 60, 1);
  height: 100%;
}

.Nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  background: rgba(60, 60, 60, 1);
  z-index: 998;
}

.Nav ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0;
  margin: 0;
}

.Nav ul li {
  margin: 0;
  padding: 20px;
  transition: all 50ms ease-in;
}

.Nav ul li:hover {
  background: orange;
  cursor: pointer;
}

@media (max-width: 600px) {
  .Nav ul {
    justify-content: center;
  }
}

h3 {
  text-align: center;
}

.Header {
  display: flex;
  justify-content: center;
  font-family: STARWARS, Helvetica, sans-serif;
  font-size: 3em;
  text-transform: uppercase;
  padding-bottom: 10px;
  border-bottom: 3px solid orange;
  height: 100%;
  margin-top: 110px;
  text-align: center;
}

.CardContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Card {
  min-width: 290px;
  height: 420px;
  border: 2px solid white;
  border-radius: 20px;
  margin: 20px;
  padding: 20px;
  transition: all 200ms ease;
  background: rgba(60, 60, 60, 1);
  /* flex: 1 1; */
}

.Card:hover {
  box-shadow: 0px 0px 20px 0px orange;
}

.Card:focus {
  box-shadow: 0px 0px 20px 0px orange;
}

.films-list {
  margin: 10px;
  margin-top: 20px;
}

.Card ul {
  padding-left: 15px;
  list-style-type: none;
  margin: 10px auto;
}

.Card li:not(:last-of-type) {
  padding-bottom: 5px;
}

.Card li:hover {
  border-left: 2px solid orange;
  border-right: 2px solid orange;
  border-radius: 5px;
  padding-left: 5px;
  cursor: pointer;
}

.Card h3 {
  border-bottom: 2px solid orange;
  padding-bottom: 10px;
}

.Card h4 {
  color: orange;
  margin-top: 10px;
  text-align: center;
}

.Card p {
  text-align: center;
  font-weight: 600;
  font-style: italic;
}

@media (min-width: 600px) {
  .Card:hover {
    transform: translateY(-1%);
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.spinner {
  width: 50px;
  height: 50px;
  background: #666;
  border-radius: 50%;
  position: relative;
  margin: 20px;
  display: inline-block;
  box-shadow: 0 0px 0 5px #333;
}

.spinner:after, .spinner:before {
  content: "";
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

.spinner-1:after {
  position: absolute;
  top: -15px;
  left: -15px;
  border: 4px solid transparent;
  border-top-color: orange;
  border-bottom-color: orange;
  -webkit-animation: spinny 1s linear infinite;
  animation: spinny 1s linear infinite;
}

@-webkit-keyframes spinny {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
  }

  50% {
    -webkit-transform: rotate(45deg) scale(1.2);
    transform: rotate(45deg) scale(1.2);
  }

  100% {
    -webkit-transform: rotate(360deg) scale(1);
    transform: rotate(360deg) scale(1);
  }
}

@keyframes spinny {
  0% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
  }

  50% {
    -webkit-transform: rotate(45deg) scale(1.2);
    transform: rotate(45deg) scale(1.2);
  }

  100% {
    -webkit-transform: rotate(360deg) scale(1);
    transform: rotate(360deg) scale(1);
  }
}

/* ////////  NAV BAR COMPS ////////// */
.About {
  position: fixed;
  max-width: 900px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  color: rgba(60, 60, 60, 1);
  z-index: 10;
  padding: 25px;
  /* border: 2px solid rgba(60,60,0,1); */
  box-shadow: 0 0 30px 5px orange;
}

.About h1,h3,h4 {
  margin: 10px;
}

.About hr {
  margin: 0;
  border: 0;
  width: 90%;
  color: orange;
  background-color: orange;
  height: 2px;
}

.About p {
  margin: 0;
  padding: 20px;
  font-size: 1em;
}

.About footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin: 0;
}

.About a {
  padding: 5px 10px;
  margin: 0 10px;
  text-decoration: none;
  color: inherit;
}

.About a:hover, .About a:focus {
  font-weight: 800;
  border-left: 2px solid orange;
  border-right: 2px solid orange;
  border-radius: 10px;
}

.About .links {
  margin-bottom: 10px;
}

.About button {
  margin: 0;
  padding: 5px 15px;
  margin-top: 20px;
  border: 2px solid orange;
  border-radius: 20px;
  transition: all 100ms ease-in;
  background: transparent;
  font: inherit;
}

.About button:hover {
  cursor: pointer;
  padding: 5px 18px;
  box-shadow: 0px 3px 10px 0px rgba(60, 60, 60, .2);
}

/* SEARCH BOX */
.SearchBox {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  /* margin-top: 60px; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: rgba(60, 60, 60, 1); */
  transition: all 200ms ease-in-out;
  z-index: 999;

}

.input {
  width: 100%;
  padding: 19px 40px;
  font: inherit;
  border: 2px solid orange;
}

.SearchBox p {
  margin: 0;
  padding: 10px 20px;
  color: inherit;
  width: 100%;
  text-align: center;
  /* border: 2px solid orange; */
  transition: all 50ms ease-in;
  background-color: rgba(60, 60, 60, 1);
}

.SearchBox p:hover {
  box-shadow: none;
  color: orange;
  font-weight: 600;
  cursor: pointer;
}

@media (min-width: 600px) {
  .SearchBox {
    margin-top: auto;
   }

  .input {
    text-align: center;
  }

  .SearchBox p {
    align-self: flex-end;
  }
  .SearchBox p:hover {
    box-shadow: none;
    color: orange;
    font-weight: 600;
  }
}
